import React from 'react';
import './MainMenu.css';
import logo from '../../img/logo_przezroczyste.png';
import MenuSection from './MenuSection.js';
import Footer from '../footer/Main';
import Home from '../Home/Home';
import Contact from '../Contact/Contact';
import { BrowserRouter, Link, Route } from 'react-router-dom'

const MainMenu = () => {
    return (
        <>
            <BrowserRouter>
                <div>
                    <div className='mainMenuContainer'>
                        <div className='logo' style={{ backgroundImage: `url(${logo})` }}></div>
                        <div className='menu'>
                            <MenuSection title='Home' link='/' />
                            <MenuSection title='Aktualności' />
                            <MenuSection title='Produkty' />
                            <MenuSection title='Usługi' />
                            <MenuSection title='Galeria' />
                            <MenuSection title='Kontakt' link='/contact' />
                        </div>
                    </div>
                    <Route path="/" exact component={Home} />
                    <Route path="/contact" component={Contact} />
                </div>
            </BrowserRouter>
            <Footer />
        </>
    );
};

export default MainMenu;