import React, { useState, useEffect } from 'react';
import './Main.css';
import sprite1 from '../../../img/sprite/1.png';
import sprite2 from '../../../img/sprite/2.png';
import sprite3 from '../../../img/sprite/3.png';
import sprite4 from '../../../img/sprite/4.png';
import sprite5 from '../../../img/sprite/5.png';
import sprite6 from '../../../img/sprite/6.png';
import sprite7 from '../../../img/sprite/7.png';
import sprite8 from '../../../img/sprite/8.png';
import sprite9 from '../../../img/sprite/9.png';
import spriteBgc1 from '../../../img/sprite/bgc/1.png';
import spriteBgc2 from '../../../img/sprite/bgc/2.png';
import spriteBgc3 from '../../../img/sprite/bgc/3.png';
import spriteBgc4 from '../../../img/sprite/bgc/4.png';
import spriteBgc5 from '../../../img/sprite/bgc/5.png';
import spriteBgc6 from '../../../img/sprite/bgc/6.png';
import spriteBgc7 from '../../../img/sprite/bgc/7.png';
import spriteBgc8 from '../../../img/sprite/bgc/8.png';
import spriteBgc9 from '../../../img/sprite/bgc/9.png';
import spriteBgc10 from '../../../img/sprite/bgc/10.png';
import spriteBgc11 from '../../../img/sprite/bgc/11.png';
import spriteBgc12 from '../../../img/sprite/bgc/12.png';
import spriteBgc13 from '../../../img/sprite/bgc/13.png';
import spriteBgc14 from '../../../img/sprite/bgc/14.png';
import spriteBgc15 from '../../../img/sprite/bgc/15.png';
import spriteBgc16 from '../../../img/sprite/bgc/16.png';
import spriteBgc17 from '../../../img/sprite/bgc/17.png';
import spriteBgc18 from '../../../img/sprite/bgc/18.png';
import spriteBgc19 from '../../../img/sprite/bgc/19.png';
import spriteBgc20 from '../../../img/sprite/bgc/20.png';

const Main = () => {

    const spriteTab = [sprite1, sprite2, sprite3, sprite4, sprite5, sprite6, sprite7, sprite8, sprite9,];
    const spriteBgcTab = [spriteBgc1, spriteBgc2, spriteBgc3, spriteBgc4, spriteBgc5, spriteBgc6, spriteBgc7, spriteBgc8, spriteBgc9, spriteBgc10, spriteBgc11, spriteBgc12, spriteBgc13, spriteBgc14, spriteBgc15, spriteBgc16, spriteBgc17, spriteBgc18, spriteBgc19, spriteBgc20];
    let i = 0;
    let j = 0;
    const [activeSprite, setActiveSprite] = useState(spriteTab[0]);
    const [activeSpriteBgc, setActiveSpriteBgc] = useState(spriteBgcTab[0]);
    const slider = () => {
        i = i + 1;
        j = j + 1;
        if (i > 8) {
            i = 0;
        };
        if (j > 19) {
            j = 0;
        };
        setActiveSprite(spriteTab[i]);
        setActiveSpriteBgc(spriteBgcTab[j]);
    };

    useEffect(() => {
        const interval = setInterval(slider, 120);
        return () => clearInterval(interval);
    }, []);

    return (
        <>
            <div className='mainContainerSection5'>
                <div className='spriteBgc' style={{ backgroundImage: `url(${activeSpriteBgc})` }}>
                    <div className='spriteContainer' style={{ backgroundImage: `url(${activeSprite})` }}></div>
                    <div className='triangle'></div>
                    <div className='rightBox'>
                        <h1>Wsparcie i wynajem pojazdów</h1>
                        <button>Dowiedz się więcej</button>
                    </div>
                </div>
            </div>
        </>
    );
}

export default Main;