import React, { useState } from 'react';
import './Main.css';
import truck1 from '../../../img/smieciarkaGeesinkGPMIV/DAF/min5.jpeg';
import truck2 from '../../../img/myjka/myjka5.jpg';
import truck3 from '../../../img/zamiatarka/zamiatarka1.jpg';
import truck4 from '../../../img/asenizacja.JPG';
import truck5 from '../../../img/hakowiec.jpg';

const Main = () => {

    const [zoom1, setZoom1] = useState(false);
    const [zoom2, setZoom2] = useState(false);
    const [zoom3, setZoom3] = useState(false);
    const [zoom4, setZoom4] = useState(false);
    const [zoom5, setZoom5] = useState(false);

    return (
        <div className='mainContainerSection4' style={{ backgroundImage: `url(${truck1})`, backgroundSize: `${zoom1 ? '50vw 60vh' : '45vw 55vh'}` }}>
            <div className='shadowMainContainer'>
                <h2 onMouseOver={() => setZoom1(true)} onMouseLeave={() => setZoom1(false)}>Śmieciarki</h2>
                <div className='leftContainer'>
                    <div className='triangleLeft'></div>
                    <div className='leftBox' style={{ backgroundImage: `url(${truck2})`, backgroundSize: `${zoom2 ? '80% 120%' : '70% 100%'}`, width: '80vw' }}>
                        <div className='shadowLeftBox'>
                            <h2 onMouseOver={() => setZoom2(true)} onMouseLeave={() => setZoom2(false)}>Myjki kontenerów</h2>
                            <div className='leftContainer'>
                                <div className='triangleLeft'></div>
                                <div className='leftBox' style={{ backgroundImage: `url(${truck3})`, backgroundSize: `${zoom3 ? '80% 120%' : '60% 100%'}`, width: '60vw' }}>
                                    <div className='shadowLeftBox'>
                                        <h2 onMouseOver={() => setZoom3(true)} onMouseLeave={() => setZoom3(false)}>Zamiatarki</h2>
                                        <div className='leftContainer'>
                                            <div className='triangleLeft'></div>
                                            <div className='leftBox' style={{ backgroundImage: `url(${truck4})`, backgroundSize: `${zoom4 ? '90% 120%' : '75% 100%'}`, width: '40vw' }}>
                                                <div className='shadowLeftBox'>
                                                    <h2 onMouseOver={() => setZoom4(true)} onMouseLeave={() => setZoom4(false)}>Pojazdy asenizacyjne</h2>
                                                    <div className='leftContainer'>
                                                        <div className='triangleLeft'></div>
                                                        <div className='leftBox' style={{ backgroundImage: `url(${truck5})`, backgroundSize: `${zoom5 ? '130% 110%' : '110% 100%'}`, width: '20vw' }}>
                                                            <div className='shadowLeftBox'>
                                                                <h2 onMouseOver={() => setZoom5(true)} onMouseLeave={() => setZoom5(false)}>Inne realizacje...</h2>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Main;