import React from 'react';
import './Main.css';
import WhiteTopBelt from '../../WhiteTopBelt/WhiteTopBelt';
import logo from '../../../img/logo_przezroczyste.png';

const Main = () => {
    return (
        <>
            <WhiteTopBelt />
            <div className='mainContainerSection1' style={{ backgroundImage: `url(${logo})` }}>
                <div className='leftBox'>
                    <h1>O nas</h1>
                    <p>Firma SHN PRESS sp. z o.o. powstała w maju 2007 roku. Spółka specjalizuje się w sprzedaży samochodów ciężarowych i specjalnych , doradztwie technicznym w zakresie kompletacji pojazdów i zabudów , świadczy również usługi w zakresie serwisu i sprzedaży części zamiennych do pojazdów branży komunalnej. Oferuje także usługi w zakresie  najmu pojazdów do zbiórki i transportu odpadów komunalnych , pojazdów do letniego i zimowego oczyszczania dróg , pojazdów kontenerowy. Głównymi odbiorcami spółki  są firmy branży komunalnej , wodociągowo – kanalizacyjnej , energetycznej , budowlanej oraz ośrodki  i szkoły nauki jazdy.</p>
                </div>
                <div className='triangle'></div>
            </div>
        </>
    );
}

export default Main;