import React from 'react';
import './Main.css';
import Logo from './Logo';
import comp1 from '../../../img/dobrowolski.PNG';
import comp2 from '../../../img/longo.jpg';
import comp3 from '../../../img/hewea.png';
import comp4 from '../../../img/iveco.png';
import comp5 from '../../../img/man.png';
import comp6 from '../../../img/mercedes.png';
import comp7 from '../../../img/renault.png';
import comp8 from '../../../img/scania.png';
import comp10 from '../../../img/volvo.png';
import comp11 from '../../../img/daf.png';

const Main = () => {
    return (
        <div className='mainContainerSection3'>
            <div className='logoTypesContainer'>
                <div className='logoTypesBelt'>
                    <Logo logo={comp1} />
                    <Logo logo={comp2} />
                    <Logo logo={comp3} />
                    <Logo logo={comp4} />
                </div>
                <div className='logoTypesBelt'>
                    <Logo logo={comp5} />
                    <Logo logo={comp6} />
                    <Logo logo={comp7} />
                    <Logo logo={comp8} />
                </div>
                <div className='logoTypesBelt'>
                    <Logo logo={comp10} />
                    <Logo logo={comp11} />
                </div>
            </div>
            <div className='triangle'></div>
            <div className='rightBox'>
                <h1>Partnerzy</h1>
                <p>Firma współpracuje z wszystkimi wiodącymi dostawcami podwozi samochodów ciężarowych takimi jak MAN , DAF , SCANIA , MERCEDES , VOLVO ,IVECO , RENAULT. Dodatkowo jest przedstawicielem wiodącego producenta pojazdów ciśnieniowych do czyszczenia kanalizacji , sprzętu do letniego i zimowego oczyszczania dróg oraz sprzętu komunalnego firmy DOBROWOLSKI  sp. z o.o.</p>
            </div>
        </div>
    );
};

export default Main;