import React, { useState, useEffect } from 'react';
import './Main.css';
import truck1 from '../../../img/slider/NAGŁÓWEK2.jpg';
import truck2 from '../../../img/slider/NAGŁÓWEK3.jpg';
import truck3 from '../../../img/slider/NAGŁÓWEK4.jpg';
import truck4 from '../../../img/slider/NAGŁÓWEK5.jpg';

const Main = () => {

    const sliderTab = [truck1, truck2, truck3, truck4];
    let i = 0;
    const [activeImg, setActiveImg] = useState(sliderTab[0]);

    const slider = () => {
        i = i + 1;
        if (i > 3) {
            i = 0;
        };
        setActiveImg(sliderTab[i]);
    };

    useEffect(() => {
        const interval = setInterval(slider, 4000);
        return () => clearInterval(interval);
    }, []);

    return (
        <>
            <div className='mainContainerSection2' style={{ backgroundImage: `url(${activeImg})` }}>
                <div className='leftBox'>
                    <h1>Nagłówek</h1>
                    <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Vivamus mollis, lectus vel feugiat consequat, erat quam cursus mi, eget luctus lorem erat eget ex. Etiam neque lorem, commodo sodales vehicula a, dignissim eu nibh. Vivamus scelerisque placerat arcu, at iaculis risus condimentum nec.</p>
                    <button>Sprawdź aktualności</button>
                </div>
                <div className='triangle'></div>
            </div>
        </>
    );
};

export default Main;